<template>
  <el-card>
    <div slot="header" class="clearfix">
      <span></span>
    </div>
    <el-row></el-row>
    <el-row type="flex" align="middle">
      <!--  -->
      <!-- 
      action API
      headers   token
      on-success 
      on-remove  
      on-preview 
      limit      
      on-exceed  
     -->
      <el-upload
        class="avatar-uploader"
        :action="uploadURL"
        :show-file-list="true"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
        ref="myUpload"
        :on-remove="handleRemove"
        :on-preview="handlePreview"
        list-type="picture-card"
        :limit="1"
        :on-exceed="limit"
        :with-credentials="true"
        :file-list="listArr"
      >
        <i class="el-icon-plus"></i>
        <div slot="tip" class="el-upload__tip">
          jpg2mb
        </div>
      </el-upload>
    </el-row>
    <el-row></el-row>
    <el-row>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
        <el-form-item prop="jianjie">
          <el-input
            type="jianjie"
            :autosize="{ minRows: 2, maxRows: 4 }"
            placeholder=""
            v-model="ruleForm.jianjie"
            maxlength="30"
            minlength="3"
          >
          </el-input>
        </el-form-item>
      </el-form>
      <el-button class="btn" type="primary" @click="add()"></el-button>
    </el-row>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="this.listArr[0].url" alt="" class="previewImg" />
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      listArr: [
        {
          url: "",
        },
      ],
      // 
      ruleForm: {
        id: localStorage.getItem("id"),
        jianjie: "",
      },
      //   
      rules: {
        jianjie: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 3,
            max: 30,
            message: "3~30",
            trigger: "blur",
          },
        ],
      },
      //   url
      uploadURL: "http://blog.losakali.top/public/index.php/blog/upload",
      previewVisible: false,
      //   id
      id: localStorage.getItem("id"),
      // url
      imgUrl: "http://blog.losakali.top/storage/app/public/tileImg/",
    };
  },
  created() {
    this.getUser();
  },
  methods: {
    //   
    async getUser() {
      const { data: res } = await this.$http.post("blog/getUser", {
        id: localStorage.getItem("id"),
      });
      if (res.status !== 200) return this.$message.error(res.message);
      this.listArr.url = res.data[0].avatar;
      this.listArr[0].url = res.data[0].avatar;
      this.ruleForm.jianjie = res.data[0].jianjie;
      this.$message.success(res.message);
    },
    // 
    handlePreview(file) {
      // file 
      // 
      this.previewVisible = true;
    },
    // 
    async handleRemove(file) {
      // file 
      console.log(file.url);
      //    
      var url = "";
      if (this.listArr[0].url) {
        //    
        url = file.url.slice(this.imgUrl.length);
        console.log(url);
      } else {
        url = file.response.data.tmp_path;
      }
      const { data: res } = await this.$http.post("blog/delImg", {
        filename: "public/tileImg/" + url,
        id: localStorage.getItem("id"),
      });
      if (res.status !== 200) return this.$message.error(res.message);
      this.$message.success(res.message);
      this.listArr[0].url = "";
    },
    //
    handleAvatarSuccess(response) {
      //   /storage/image/ 
      this.listArr[0].url = response.data.url;
      this.$message.success(response.message);
      this.addUserImg(response.data.url);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    //
    clearFiles() {
      this.$refs["myUpload"].clearFiles();
    },
    limit() {
      this.$message.warning("");
    },
    //  
    async addUserImg(avatar) {
      const { data: res } = await this.$http.post("blog/addUserImg", {
        id: this.id,
        avatar: this.listArr[0].url,
      });
      if (res.status !== 200) return this.$message.error(res.message);
      //  
    },
    // 
    add() {
      this.$refs.ruleForm.validate(async (valid) => {
        if (!valid) return;
        // /
        const { data: res } = await this.$http.post(
          "blog/addJianJie",
          this.ruleForm
        );
        if (res.status !== 200) return this.$message.error(res.message);
        this.$message.success(res.message);
      });
    },
  },
};
</script>

<style lang="less" scoped>
// 
.el-card {
  width: 60rem;
  margin: 0 auto;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
//
.el-row {
  margin-bottom: 2.666667rem;
}
// 
.btn {
  margin-top: 0.533333rem;
}
</style>