<template>
  <div class="box">
    <el-container class="home-container">
      <!--  -->
      <el-container>
        <el-aside :width="isCollapse ? '9rem' : '300px'">
          <!--  -->
          <el-button
            class="zedie"
            type="primary"
            :icon="isCollapse ? 'el-icon-caret-right' : 'el-icon-caret-left'"
            circle
            @click="zhedie()"
          ></el-button>
          <!--  -->
          <div v-if="avatar" class="tou">
            <img :src="avatar" alt="" />
          </div>
          <div v-else class="tou">
            <img src="../assets/user.png" alt="" />
          </div>
          <el-menu
            background-color="#333744"
            text-color="#fff"
            active-text-color="#409BFF"
            :unique-opened="true"
            :router="true"
            :default-active="$route.path"
            :collapse="isCollapse"
            :collapse-transition="false"
          >
            <!--   -->
            <el-submenu v-if="name" index="1">
              <template slot="title">
                <i class="el-icon-location"></i>
                <!--  -->
                <span>{{ name }}</span>
              </template>
              <!--  -->

              <el-menu-item index="/release">
                <template slot="title">
                  <i class="el-icon-edit-outline"></i>
                  <!--  -->
                  <span></span>
                </template>
              </el-menu-item>
              <el-menu-item index="/myblog">
                <template slot="title">
                  <i class="el-icon-document-copy"></i>
                  <!--  -->
                  <span></span>
                </template>
              </el-menu-item>
              <el-menu-item index="/myset">
                <template slot="title">
                  <i class="el-icon-setting"></i>
                  <!--  -->
                  <span></span>
                </template>
              </el-menu-item>
              <el-menu-item @click="logout">
                <template slot="title">
                  <i class="el-icon-monitor"></i>
                  <!--  -->
                  <span></span>
                </template>
              </el-menu-item>
            </el-submenu>

            <!--   -->
            <el-menu-item v-else index="/login">
              <i class="el-icon-location"></i>
              <!--  -->
              <span slot="title"></span>
            </el-menu-item>
            <!--  -->
            <el-menu-item index="/welcome">
              <i class="el-icon-s-home"></i>
              <span slot="title"></span>
            </el-menu-item>
            <!--  -->
            <el-menu-item index="/photo">
              <i class="el-icon-camera-solid"></i>
              <span slot="title"></span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-main>
          <el-button
            class="btn"
            type="danger"
            :icon="drawer ? 'el-icon-caret-top' : 'el-icon-arrow-down'"
            circle
            @click="open()"
          ></el-button>
          <transition mode="out-in">
            <!--  -->
            <router-view class="center"></router-view>
          </transition>
        </el-main>
      </el-container>
    </el-container>
    <!--  -->
    <el-drawer
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
      size="30%"
      :with-header="false"
    >
      <div class="box-card">
        <el-row type="flex" justify="center">
          <!--  -->
          <div v-if="avatar" class="tou">
            <img :src="avatar" alt="" />
          </div>
          <div v-else class="tou">
            <img src="../assets/user.png" alt="" />
          </div>
        </el-row>
        <el-row type="flex" justify="center">
          <h4 v-if="jianjie" v-text="jianjie"></h4>
        </el-row>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      isCollapse: false,
      name: localStorage.getItem("name"),
      avatar: localStorage.getItem("avatar"),
      // 
      drawer: false,
      // 
      direction: "ttb",
      jianjie: localStorage.getItem("jianjie"),
      userimg: "../assets/user.png",
    };
  },
  methods: {
    async logout() {
      const { data: res } = await this.$http.post("logout");
      if (res.status !== 200) {
        return this.$message.error(res.message);
      }
      // sessionStorage token
      localStorage.clear();
      // 
      this.$router.push("/login");
      this.$message.success(res.message);
    },
    // 
    handleClose() {
      this.drawer = false;
    },
    // 
    open() {
      this.drawer = !this.drawer;
    },
    // 
    zhedie() {
      this.isCollapse = !this.isCollapse;
    },
  },
};
</script>

<style lang="less" scoped>
// align-items:     y
// justify-content: x
.home-container {
  height: 100%;
}
.blog {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80%;
}
.btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 1.333333rem;
}
// 
.v-enter {
  opacity: 0;
}
.v-enter-active {
  transition: 0.5s;
}
.v-enter-to {
  opacity: 1;
}
.v-leave {
  opacity: 1;
}
.v-leave-to {
  opacity: 0;
}
.v-leave-active {
  transition: 0.5s;
}
// card
.box-card {
  width: 100%;
  height: 100%;
  background: url("../assets/gradient_spots.jpg");
}
// 
.el-aside {
  position: relative;
  background-color: #333744;
  height: 100%;
  .el-menu {
    border-right: none;
  }
}
.box {
  height: 100%;
}
.zedie {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}
// 
.tou {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  border: 0.266667rem solid #fff;
  overflow: hidden;
  margin: 0 auto;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>