<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>{{ username }}</span>
        <span style="float: right; padding: 3px 0" type="text"
          >{{ total }}</span
        >
      </div>
      <el-table :data="blogList" style="width: 100%" :key="Math.random()">
        <el-table-column label="">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>
              <el-tag :type="tagType(scope.row.type_id)" effect="plain">
                {{ tagLabel(scope.row.type_id) }}
              </el-tag>
            </span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <i class="el-icon-edit"></i>
            <span style="margin-left: 10px">{{ scope.row.blog_title }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <img
              :src="scope.row.cover_image"
              alt=""
              style="margin-left: 10px; width: 150px; height: 84px"
            />
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="small"
              type="success"
              icon="el-icon-search"
              @click="blog(scope.row.blog_id)"
              ></el-button
            >
            <el-button
              size="small"
              type="warning"
              icon="el-icon-search"
              @click="openDialog(scope.row.blog_id)"
              ></el-button
            >
            <el-button
              size="small"
              type="danger"
              icon="el-icon-search"
              @click="delBlog(scope.row.blog_id)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 
      
      size-change 
      current-change 
      current-page 
        pagenum 
      page-size 
      layout 
      total 
     -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 
         
        visible.sync  
        before-close 
        @close 
     -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="dialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormrules"
        ref="editFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-form-item label="" prop="blog_title">
          <el-input v-model="editForm.blog_title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type_id">
          <!-- 
                
                options 
                props  
                expand-trigger 
                v-model 
               -->
          <el-cascader
            v-model="editForm.type_id"
            :options="blogType"
            :props="cateProps"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="">
          <!--  -->
          <!-- 
      action API
      headers   token
      on-success 
      on-remove  
      on-preview 
      limit      
      on-exceed  
     -->
          <el-row>
            <el-col :span="8">
              <div class="imgbox">
                <img :src="editForm.cover_image" @click="handlePreview" />
              </div>
            </el-col>
            <el-col :span="16">
              <el-upload
                class="avatar-uploader"
                :action="uploadURL"
                :show-file-list="true"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
                ref="myUpload"
                list-type="picture"
                :limit="1"
                :on-exceed="limit"
                :on-remove="handleRemove"
                :with-credentials="true"
              >
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">
                  jpg2mb
                </div>
              </el-upload>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="">
          <!--  -->
          <quill-editor v-model="editForm.blog_content"></quill-editor>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="editBlog"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="editForm.cover_image" alt="" class="previewImg" />
    </el-dialog>
  </div>
</template>

<script>
export default {
  inject: ["reload"],
  data() {
    return {
      // 
      options: [
        {
          type: "",
          label: "",
        },
        {
          type: "success",
          label: "",
        },
        {
          type: "info",
          label: "",
        },
        {
          type: "danger",
          label: "",
        },
        {
          type: "warning",
          label: "",
        },
      ],
      previewVisible: false, //
      // 
      cateProps: {
        // 
        expandTrigger: "hover",
        // 
        label: "type_name",
        // 
        value: "type_id",
      },
      // 
      blogType: "",
      // 
      editForm: {
        blog_id: "",
        blog_title: "",
        type_id: "",
        cover_image: "",
        blog_content: "",
      },
      //   
      editFormrules: {
        blog_title: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        type_id: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      dialogVisible: false,
      blogList: [],
      username: localStorage.getItem("name"),
      total: 0,
      queryInfo: {
        id: localStorage.getItem("id"),
        size: 5,
        page: 1,
      },
      // url
      uploadURL: "http://blog.losakali.top/public/index.php/blog/upload",
      // headers
      // url
      imgUrl: "http://blog.losakali.top/storage/app/",
    };
  },
  created() {
    //   
    this.getBlog();
    // 
    this.getBlogType();
  },
  // 
  computed: {
    tagLabel() {
      return function (item) {
        return this.options[item - 1]["label"];
      };
    },
    tagType() {
      return function (item) {
        return this.options[item - 1]["type"];
      };
    },
  },
  methods: {
    async getBlog() {
      console.log(this.queryInfo.id);
      const { data: res } = await this.$http.post(
        "blog/myBlog",
        this.queryInfo
      );
      if (res.status !== 200) return;
      console.log(res.data);
      this.blogList = res.data.data;
      this.total = res.data.total;
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.size = newSize;
      this.getBlog();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getBlog();
    },
    // 
    blog(blog_id) {
      this.$router.push("/blog/" + blog_id);
    },
    // 
    dialogClosed() {
      this.getBlog();
    },
    // 
    editBlog() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error("");
        }
        this.editForm.type_id = this.editForm.type_id[0];
        console.log(this.editForm);
        const { data: res } = await this.$http.post(
          "blog/upBlog",
          this.editForm
        );
        if (res.status !== 200) return this.$message.error(res.message);
        this.$message.success(res.message);
        this.getBlog();
        this.dialogVisible = false;
      });
    },
    // 
    async openDialog(id) {
      this.dialogVisible = true;
      const { data: res } = await this.$http.get("blog/blogId/" + id);
      if (res.status !== 200) return this.$message.error(res.message);
      this.editForm.blog_title = res.data[0].blog_title;
      this.editForm.cover_image = res.data[0].cover_image;
      this.editForm.blog_content = res.data[0].blog_content;
      this.editForm.type_id = res.data[0].type_id;
      this.editForm.blog_id = res.data[0].blog_id;
    },
    // 
    async getBlogType() {
      const { data: res } = await this.$http.post("blog/blogType");
      if (res.status !== 200) return this.$message.error(res.message);
      this.blogType = res.data;
    },
    // 
    handlePreview() {
      // file 
      // 
      this.previewVisible = true;
    },
    //
    async handleAvatarSuccess(response) {
      if (!this.editForm.cover_image == "") {
        var uslStr = this.editForm.cover_image.slice(this.imgUrl.length);
        const { data: res } = await this.$http.post("blog/imgdel", {
          filename: uslStr,
          blog_id: this.editForm.blog_id,
        });
        if (res.status !== 200) return this.$message.error(res.message);
        this.$message.success("");
        this.getBlog();
      }
      //   /storage/image/ 
      this.editForm.cover_image = response.data.url;
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    //
    clearFiles() {
      this.$refs["myUpload"].clearFiles();
    },
    limit() {
      this.$message.warning("");
    },
    // 
    async handleRemove(file) {
      // file 
      console.log(file);
      const { data: res } = await this.$http.post("blog/imgdel", {
        filename: "public/tileImg/" + file.response.data.tmp_path,
      });
      if (res.status !== 200) return this.$message.error(res.message);
      this.$message.success(res.message);
      this.editForm.cover_image = "";
    },
    // 
    async delBlog(id) {
      // $confirm element
      // 
      //   confirm
      // catch 
      // err=>err  return err  return 
      //  cancel
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      // 
      if (confirmResult !== "confirm") return this.$message.info("");
      // 
      const { data: res } = await this.$http.post("blog/delblog", {
        blog_id: id,
      });
      if (res.status !== 200) return this.$message.error(res.message);
      this.$message.success(res.message);
      this.reload();
    },
  },
};
</script>

<style lang="less" scoped>
.el-card {
  padding: 0 8rem;
}
.text {
  font-size: 0.373333rem;
}

.item {
  margin-bottom: 0.48rem;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.el-pagination {
  margin-top: 2.666667rem;
}
.previewImg {
  width: 100%;
}

.imgbox {
  width: 8rem;
  overflow: hidden;
  img {
    width: 100%;
    height: 5.333333rem;
  }
}
.box-card {
  position: relative;
}
</style>