<template>
  <div>
    <el-card>
      <!-- 
         
        closable 
      -->
      <el-alert title="" type="info" center show-icon :closable="false">
      </el-alert>
      <!-- 
        
        active  1
        activeIndex - 0   
       -->
      <el-steps :active="activeIndex - 0" align-center>
        <el-step title="" icon="el-icon-s-home"></el-step>
        <el-step title="" icon="el-icon-s-order"></el-step>
        <el-step title="" icon="el-icon-picture"></el-step>
        <el-step title="" icon="el-icon-edit"></el-step>
      </el-steps>
      <!-- 
         
        label-position 
        prop 
      -->
      <el-form
        :model="addForm"
        :rules="addFormrules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <!-- 
          tab 
          tab-position tab
          before-leave tab
        -->
        <el-tabs
          :tab-position="'left'"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
        >
          <el-tab-pane label="" name="1">
            <el-form-item label="" prop="blog_title">
              <el-input v-model="addForm.blog_title"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item label="" prop="type_id">
              <!-- 
                
                options 
                props  
                expand-trigger 
                v-model 
               -->
              <el-cascader
                v-model="addForm.type_id"
                :options="blogType"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!--  -->
            <!-- 
      action API
      headers   token
      on-success 
      on-remove  
      on-preview 
      limit      
      on-exceed  
      :with-credentials="true" cookie
     -->
            <el-upload
              class="avatar-uploader"
              :action="uploadURL"
              :show-file-list="true"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
              ref="myUpload"
              :on-remove="handleRemove"
              :on-preview="handlePreview"
              list-type="picture"
              :limit="1"
              :on-exceed="limit"
              :with-credentials="true"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg2mb
              </div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.blog_content"></quill-editor>
            <el-button type="primary" class="btn" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>

      <!--  -->
      <el-dialog title="" :visible.sync="previewVisible" width="50%">
        <img :src="addForm.cover_image" alt="" class="previewImg" />
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cateProps: {
        // 
        expandTrigger: "hover",
        // 
        label: "type_name",
        // 
        value: "type_id",
      },
      // 
      blogType: [],
      // 
      addForm: {
        userid: localStorage.getItem("id"),
        blog_status: 0,
        blog_title: "",
        type_id: "",
        cover_image: "",
        blog_content: "",
      },
      // 
      addFormrules: {
        blog_title: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        type_id: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      activeIndex: "1", //
      previewVisible: false, //
      content: "",
      // url
      uploadURL: "http://blog.losakali.top/public/index.php/blog/upload",
    };
  },
  created() {
    // 
    this.getBlogType();
  },
  methods: {
    // tab
    //     
    beforeTabLeave(activeName, oldActiveName) {
      // // 
      // console.log(activeName);
      // // 
      // console.log(oldActiveName);
      //  
      if (oldActiveName === "2" && this.addForm.type_id == "") {
        this.$message.error("");
        return false;
      }
      if (oldActiveName === "3" && this.addForm.cover_image == "") {
        this.$message.error("");
        return false;
      }
    },
    //  
    handleChange() {
      console.log(this.addForm.type_id);
    },
    // 
    async getBlogType() {
      const { data: res } = await this.$http.post("blog/blogType");
      if (res.status !== 200) return this.$message.error(res.message);
      console.log(res);
      this.blogType = res.data;
      console.log(this.blogType);
    },
    // 
    add() {
      console.log(this.addForm);
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error("");
        }
        this.addForm.blog_status = 1;
        this.addForm.type_id = this.addForm.type_id[0];
        const { data: res } = await this.$http.post(
          "blog/addBlog",
          this.addForm
        );
        if (res.status !== 200) return this.$message.error(res.message);
        this.$message.success(res.message);
        this.$router.push("/home");
      });
    },
    // 
    handlePreview(file) {
      // file 
      // 
      this.previewVisible = true;
    },
    // 
    async handleRemove(file) {
      // file 
      console.log(file);
      const { data: res } = await this.$http.post("blog/delImg", {
        filename: "public/tileImg/" + file.response.data.tmp_path,
      });
      if (res.status !== 200) return this.$message.error(res.message);
      this.$message.success(res.message);
      this.addForm.cover_image = "";
    },
    //
    handleAvatarSuccess(response) {
      //   /storage/image/ 
      this.addForm.cover_image = response.data.url;
      this.$message.success(response.message);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    //
    clearFiles() {
      this.$refs["myUpload"].clearFiles();
    },
    limit() {
      this.$message.warning("");
    },
  },
};
</script>

<style lang="less" scoped>
.btn {
  margin-top: 0.266667rem;
}
.previewImg {
  width: 100%;
}
.el-card {
  padding: 0 13.333333rem;
}
</style>