<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/user.png" alt="" />
      </div>
      <!--  -->
      <!-- 
          label-width 
          :model=""
          :rules=""
          prop=""
          ref=""
       -->
      <el-form
        label-width="0px"
        class="login_form"
        :model="loginForm"
        :rules="loginFormRules"
        ref="loginFormRef"
      >
        <!--  -->
        <el-form-item prop="email">
          <el-input
            v-model="loginForm.email"
            prefix-icon="iconfont icon-wode"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            prefix-icon="iconfont icon-mima"
            type="password"
          ></el-input>
        </el-form-item>
        <!--  -->
        <!-- element -->
        <el-form-item class="btn">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="success" @click="addDialogVisible = true"
            ></el-button
          >
          <el-button type="info" @click="resetLoginForm('loginFormRef')"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>

    <!-- 
         
        visible.sync  
        before-close 
        @close 
     -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="30%"
      @close="addDialogClosed"
    >
      <!--  -->
      <!-- :rules -->
      <!-- prop -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password_confirmation">
          <el-input v-model="addForm.password_confirmation"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
      </el-form>
      <!-- slot="footer"  -->
      <span slot="footer" class="dialog-footer">
        <!--  -->
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        email: "12345678@qq.com",
        password: "12345678",
      },
      //   
      //   required 
      //   message
      //   trigger 
      loginFormRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          //   
          {
            min: 10,
            max: 20,
            message: " 10  20 ",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 8,
            max: 15,
            message: "8~15",
            trigger: "blur",
          },
        ],
      },
      // 
      addDialogVisible: false,
      addForm: {
        name: "",
        password: "",
        password_confirmation: "",
        email: "",
      },
      addFormRules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 3,
            max: 10,
            message: "3~10",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 8,
            max: 15,
            message: "8~15",
            trigger: "blur",
          },
        ],
        password_confirmation: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 8,
            max: 15,
            message: "8~15",
            trigger: "blur",
          },
        ],
        email: [
          {
            required: true,
            type: "email",
            message: "",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },
  methods: {
    //   
    resetLoginForm(formName) {
      // resetFields  

      this.$refs[formName].resetFields();
    },
    // 
    login() {
      // validate()      function(valid){}
      //   true false
      this.$refs.loginFormRef.validate(async (valid) => {
        //    falsereturn 
        if (!valid) return;
        //  
        const { data: res } = await this.$http.post("login", this.loginForm);
        console.log(res);
        // 
        if (res.status !== 200) return this.$message.error(res.message);
        // 
        this.$message.success(res.message);
        // 1. token sessionStorage  sessionStorage
        console.log(res);
        localStorage.setItem("name", res.data[0].name);
        localStorage.setItem("id", res.data[0].id);
        localStorage.setItem("avatar", res.data[0].avatar);
        localStorage.setItem("jianjie", res.data[0].jianjie);
        // idwindow.sessionStorage.setItem("id", res.data.id);  id
        // 2.API
        // 3.token tokensessionStorage
        // 4. /home $router.push()
        // tokenF12 Application->Session Storage 
        this.$router.push("/home").catch(() => {});
      });
    },
    addDialogClosed() {
      // formrefresetFields
      this.$refs.addFormRef.resetFields();
    },
    addUser() {
      // 
      // formrefvalidate
      // true false
      this.$refs.addFormRef.validate(async (valid) => {
        //console.log(valid);
        //  
        if (!valid) return;
        //  
        // : :register :post name password password_confirmation email
        // statuts201
        // 
        if (!(this.addForm.password == this.addForm.password_confirmation)) {
          return this.$message.error("");
        }
        const { data: res } = await this.$http.post("register", this.addForm);
        if (res.status !== 200) {
          // res.meta.msg
          return this.$message.error(res.message);
        }
        console.log(res.data.name);
        // 
        this.addDialogVisible = false;
        // 
        this.$message.success(res.message);
        console.log(res.data);
        // 1. token sessionStorage  sessionStorage
        localStorage.setItem("name", res.data.name);
        localStorage.setItem("id", res.data.id);
        localStorage.setItem("avatar", res.avatar);
        localStorage.setItem("jianjie", res.jianjie);
        this.$router.push("/home");
      });
    },
  },
};
</script>

<style lang="less" scoped>
.login_box {
  width: 24rem;
  height: 18rem;
  background-color: #fff;
  border-radius: 1rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 3.466667rem;
    width: 3.466667rem;
    border: 1px solid #eee;
    border-radius: 50%;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 0.533333rem;
  //   
  box-sizing: border-box;
}
.btn {
  display: flex;
  //   
  justify-content: flex-end;
}
</style>