<template>
  <div class="box">
    <el-card>
      <div id="blog" class="ql-snow ql-editor">
        <!-- divclass ql-snow ql-editor -->
      </div>
    </el-card>
    <el-card style="margin-top: 20px">
      <div class="content">
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          :inline="true"
        >
          <el-form-item prop="comment_content">
            <el-input
              type="textarea"
              :placeholder="pltext"
              v-model="addForm.comment_content"
              maxlength="30"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </el-form>
        <el-button type="primary" class="btn" @click="add()"
          ></el-button
        >
      </div>
    </el-card>
    <el-table
      :data="comment"
      style="width: 100%; margin-bottom: 20px"
      border
      default-expand-all
    >
      <!-- :tree-props="{ children: 'children', hasChildren: 'hasChildren' }" -->
      <el-table-column label="id" sortable width="180">
        <template slot-scope="scope">
          <p>{{ scope.row.blog_id }}</p>
          <p>{{ scope.row.createtime }}</p>
        </template>
      </el-table-column>
      <el-table-column label="" sortable width="180">
        <template slot-scope="scope">
          {{ scope.row.comment_content }}
        </template>
      </el-table-column>
      <el-table-column label="id" sortable width="180">
        <template slot-scope="scope">
          {{ scope.row.replyid }}
        </template>
      </el-table-column>
      <el-table-column sortable width="180">
        <el-button type="success" size="mini" round></el-button>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      pltext: "",
      blog_id: "",
      blog: "",
      // 
      comment: [],
      // 
      addForm: {
        // id
        userid: localStorage.getItem("id"),
        // id
        blog_id: "",
        //   id
        replyid: "",
        // /
        comment_content: "",
      },
      // 
      addFormRules: {
        comment_content: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 5,
            max: 30,
            message: "530",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {
    this.getBlog();
  },
  methods: {
    async getBlog() {
      //  this.$route.params.   r
      const { data: res } = await this.$http.get(
        "blog/blogId/" + this.$route.params.id
      );
      if (res.status !== 200) return this.$message.error(res.message);
      this.blog = res.data;
      // id
      this.addForm.blog_id = res.data[0].blog_id;
      this.addForm.replyid = res.data[0].id;
      // 
      this.comment = res.comment;
      document.getElementById("blog").innerHTML = res.data[0].blog_content;
      console.log(res);
    },
    // 
    add() {
      if (!localStorage.getItem("id"))
        return this.$message.error("");
      this.addForm.userid = localStorage.getItem("id");
      this.$refs.addFormRef.validate(async (valid) => {
        //console.log(valid);
        //  
        if (!valid) return;
        console.log(this.addForm);
        const { data: res } = await this.$http.post(
          "blog/content",
          this.addForm
        );
        if (res.status !== 200) return this.$message.error(res.message);
        // 
        this.$refs.addFormRef.resetFields();
        this.getBlog();
        this.$message.success(res.message);
      });
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  width: 80rem;
  height: 100%;
  margin: 0 auto;
}
.content {
  display: flex;
  align-items: center;
  .btn {
    margin-left: 0.266667rem;
  }
  .el-form-item {
    margin-bottom: 0;
  }
}
.el-card {
  position: relative;
}
.el-table {
  position: relative;
}
</style>