import Vue from 'vue'
import VueRouter from 'vue-router'
// 
import Login from './components/Login.vue'
// 
import Home from './components/Home.vue'
// 
import Welcome from './components/Welcome.vue'
// 
import Release from './components/my/Release.vue'
// 
import Blog from './components/my/Blog.vue'
// 
import MyBlog from './components/my/MyBlog'
// 
import Myset from './components/my/Myset'
// 
import Photo from './components/my/Photo'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/home',
    component: Home,
    // 
    redirect: '/welcome',
    // 
    children: [
      {
        path: '/welcome',
        component: Welcome
      },
      {
        path: '/release',
        component: Release
      },
      {
        path: '/blog/:id',
        component: Blog
      }, {
        path: '/myblog',
        component: MyBlog
      }, {
        path: '/myset',
        component: Myset
      }, {
        path: '/photo',
        component: Photo
      }
    ]
  }
]
// 
const router = new VueRouter({
  routes
})
// 
router.beforeEach((to, from, next) => {
  //   
  if (to.path === '/login') return next();
  if (to.path === '/home') return next();
  // // token
  // const tokenStr = window.sessionStorage.getItem("name");
  // // token 
  // if (!tokenStr) return next('/login');
  // // token 

  next();
});
// 
export default router
